import { sdk } from '../data/sdk';
import { observable, computed, action } from 'mobx';

class ObservableStore {
    @observable appTags = [];
    @observable filteringTag = '';

    constructor() {
        this.origin = sdk;
        this.setUniqueTags(sdk);
    }

    @action setUniqueTags(data) {
        data.forEach((element) => {
            element.tags.forEach((item) => {
                if (this.appTags.indexOf(item) == -1) this.appTags.push(item);
            });
        });
    }

    @computed get appData() {
        return this.origin.filter(function (el) {
            return (el.tags.indexOf('tag1') > -1);
        });
    }
}
const observableStore = window.store = new ObservableStore();
export default observableStore;



